import { db } from './firebase';
import { collection, getDocs, doc, getDoc } from 'firebase/firestore';

// ÌÉÄÏûÖ Ï†ïÏùò (serverCacheÏôÄ ÎèôÏùºÌïòÍ≤å Ïú†ÏßÄ)
export interface Issue {
    id: string;
    edition_code: string;
    edition_name: string;
    article_count: number;
    published_at: string;
    released_at?: string;
    updated_at?: string;
    status: 'preview' | 'released';
    date: string;
}

export interface Article {
    article_id?: string;
    id?: string;
    title_ko?: string;
    title?: string;
    summary?: string;
    url?: string;
    impact_score?: number;
    zero_echo_score?: number;
    published_at?: string;
    publish_id?: string;
    [key: string]: any;
}

// _meta Î¨∏ÏÑú Íµ¨Ï°∞
interface MetaIssue {
    code: string;
    name: string;
    count: number;
    updated_at: string;
    status: 'preview' | 'released';
}

interface MetaDoc {
    issues: MetaIssue[];
    latest_updated_at: string;
}

const COLLECTION_PUBLICATIONS = 'publications';

/**
 * Í≥µÍ∞úÎêú(released) ÌöåÏ∞® Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
 * [NEW] _meta Î¨∏ÏÑúÏóêÏÑú ÏßÅÏ†ë Ï°∞Ìöå (1 READ, Í≤ΩÎüâ)
 */
export async function fetchPublishedIssues(): Promise<{ issues: Issue[], latestUpdatedAt: string | null }> {
    try {
        console.log('üî• [Firestore] Fetching _meta document...');

        // _meta Î¨∏ÏÑúÏóêÏÑú Î™©Î°ù Ï°∞Ìöå (1 READ)
        const metaDoc = await getDoc(doc(db, COLLECTION_PUBLICATIONS, '_meta'));

        if (!metaDoc.exists()) {
            console.log('‚ö†Ô∏è [Firestore] _meta document not found, falling back to full scan...');
            return await fetchPublishedIssuesFallback();
        }

        const metaData = metaDoc.data() as MetaDoc;
        const latestUpdate = metaData.latest_updated_at || null;

        // released ÏÉÅÌÉúÏù∏ Í≤ÉÎßå ÌïÑÌÑ∞ÎßÅ
        const releasedMeta = (metaData.issues || []).filter(i => i.status === 'released');

        // Issue ÌòïÏãùÏúºÎ°ú Î≥ÄÌôò (ÏÉÅÏÑ∏ Ï†ïÎ≥¥Îäî ÌöåÏ∞® Î¨∏ÏÑúÏóêÏÑú Í∞ÄÏ†∏ÏôÄÏïº Ìï®)
        const issues: Issue[] = releasedMeta.map(meta => ({
            id: meta.code,
            edition_code: meta.code,
            edition_name: meta.name,
            article_count: meta.count,
            published_at: meta.updated_at,
            updated_at: meta.updated_at,
            status: meta.status,
            date: meta.code.replace(/_\d+$/, '').replace(/(\d{2})(\d{2})(\d{2})/, '20$1-$2-$3')
        }));

        // published_at ÎÇ¥Î¶ºÏ∞®Ïàú Ï†ïÎ†¨
        issues.sort((a, b) => {
            const dateA = new Date(a.published_at || 0).getTime();
            const dateB = new Date(b.published_at || 0).getTime();
            return dateB - dateA;
        });

        console.log(`‚úÖ [Firestore] Found ${issues.length} released issues from _meta`);
        return { issues, latestUpdatedAt: latestUpdate };

    } catch (error) {
        console.error('‚ùå [Firestore] Failed to fetch _meta:', error);
        return await fetchPublishedIssuesFallback();
    }
}

/**
 * Ìè¥Î∞±: Ï†ÑÏ≤¥ publications Ïä§Ï∫î (Í∏∞Ï°¥ Î∞©Ïãù, _meta ÏóÜÏùÑ Îïå)
 */
async function fetchPublishedIssuesFallback(): Promise<{ issues: Issue[], latestUpdatedAt: string | null }> {
    try {
        console.log('üî• [Firestore] Fallback: scanning all publications...');
        const snapshot = await getDocs(collection(db, COLLECTION_PUBLICATIONS));

        let allIssues: Issue[] = [];
        let latestUpdate: string | null = null;

        snapshot.forEach((docSnap) => {
            const docId = docSnap.id;
            // _meta, _article_ids Ï†úÏô∏
            if (docId.startsWith('_')) return;

            const data = docSnap.data();
            allIssues.push({
                id: docSnap.id,
                ...data
            } as Issue);
        });

        const releasedIssues = allIssues.filter(issue => issue.status === 'released');

        releasedIssues.sort((a, b) => {
            const dateA = new Date(a.published_at || 0).getTime();
            const dateB = new Date(b.published_at || 0).getTime();
            return dateB - dateA;
        });

        for (const issue of releasedIssues) {
            if (issue.updated_at) {
                if (!latestUpdate || new Date(issue.updated_at) > new Date(latestUpdate)) {
                    latestUpdate = issue.updated_at;
                }
            }
        }

        console.log(`‚úÖ [Firestore] Fallback found ${releasedIssues.length} released issues`);
        return { issues: releasedIssues, latestUpdatedAt: latestUpdate };
    } catch (error) {
        console.error('‚ùå [Firestore] Fallback failed:', error);
        return { issues: [], latestUpdatedAt: null };
    }
}

/**
 * ÌäπÏ†ï ÌöåÏ∞®(issueId)Ïùò Í∏∞ÏÇ¨ Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
 * [NEW] publications Î¨∏ÏÑúÏùò ÎÇ¥Ïû•Îêú articles Î∞∞Ïó¥ ÏÇ¨Ïö© (1 READ)
 */
export async function fetchArticlesByIssueId(issueId: string): Promise<Article[]> {
    try {
        // console.log(`üî• [Firestore] Fetching articles for issue: ${issueId}`);

        // ÌöåÏ∞® Î¨∏ÏÑúÏóêÏÑú ÏßÅÏ†ë articles Î∞∞Ïó¥ ÏùΩÍ∏∞ (1 READ)
        const pubDoc = await getDoc(doc(db, COLLECTION_PUBLICATIONS, issueId));

        if (!pubDoc.exists()) {
            console.log(`‚ùå [Firestore] Publication not found: ${issueId}`);
            return [];
        }

        const pubData = pubDoc.data();
        const articles: Article[] = pubData.articles || [];

        // article_id ÌïÑÎìú Ï†ïÍ∑úÌôî
        return articles.map(art => ({
            ...art,
            article_id: art.id || art.article_id,
            id: art.id || art.article_id
        }));

    } catch (error) {
        console.error(`‚ùå [Firestore] Failed to fetch articles for ${issueId}:`, error);
        return [];
    }
}


/**
 * ÏµúÏã† Î≥ÄÍ≤Ω ÏÇ¨Ìï≠ ÌôïÏù∏ 
 * [NEW] _meta Î¨∏ÏÑúÏùò latest_updated_at ÌôïÏù∏ (1 READ, Í≤ΩÎüâ)
 */
export async function checkLatestUpdate(): Promise<string | null> {
    try {
        // _meta Î¨∏ÏÑúÏóêÏÑú latest_updated_at ÌôïÏù∏ (1 READ)
        const metaDoc = await getDoc(doc(db, COLLECTION_PUBLICATIONS, '_meta'));

        if (metaDoc.exists()) {
            const metaData = metaDoc.data() as MetaDoc;
            return metaData.latest_updated_at || null;
        }

        // _meta ÏóÜÏúºÎ©¥ Ìè¥Î∞±
        console.log('‚ö†Ô∏è [Firestore] _meta not found, falling back...');
        const { latestUpdatedAt } = await fetchPublishedIssuesFallback();
        return latestUpdatedAt;

    } catch (error) {
        console.error('‚ùå [Firestore] checkLatestUpdate failed:', error);
        return null;
    }
}
